

*{
    margin: 0;
    box-sizing: border-box;
    width: 100vw;
}

.header {
    width: 100%;
    background-color: #F55A5A;
    display: flex;
    padding: 7px 0px;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    
}

.title {
    width: auto;
    color: white;
    padding: 0 20px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 100%;
    line-height: 18px;
    letter-spacing: -0.035em;
    overflow-y: hidden;
}

.title-img{
    width: auto;
}

