*{
    margin: 0;
    box-sizing: border-box;
    width: 100vw;
}

.journey {
    padding: 3% 5% 3% 5%;
    width: 100%;
    font-family: "Inter";
    font-style: "normal";
    display: flex;
    justify-content:flex-start;
    flex-wrap: wrap;
}

.journey-item{
    width: 50%;
}

.travel-img{
    width: 25%;
    align-self: center;
}

.details{
    padding: 5% 3%;
    width: 75%;
    align-self: center;
    max-width: 900px;
}

.sub{
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
}

.sub-item{
    width: auto;
}

.location-img{
    height: 2.4vmin;
}

.location{
    padding: 0 2% 0 1%;
    font-weight: 400;
    font-size: 2.3vmin;
    line-height: 150%;
    letter-spacing: 0.17em;
    color: #2B283A;
}

.gmap-link{
    font-weight: 400;
    font-size: 2.3vmin;
    line-height: 120%;
    text-decoration-line: underline;
    color: #918E9B;
}

.journey-title{
    margin: 1% 0 2% 0;
    font-weight: 700;
    font-size: 3.5vmin;
    line-height: 150%;
    color: #2B283A;
}

.date{
    font-weight: 600;
    font-size: 2.6vmin;
    line-height: 100%;
    color: #2B283A;
    padding: 0.4% 0 0.4% 0;

}

.description{
    font-weight: 400;
    font-size: 2.3vmin;
    line-height: 150%;
    color: #2B283A;
    padding: 5px 0;
}


